        const EMOJIS = "üòÄ üòÉ üòÑ üòÅ üòÜ üòÖ üòÇ ü§£ ‚ò∫Ô∏è üòä üòá üôÇ üôÉ üòâ üòå üòç üòò üòó üòô üòö üòã üòú üòù üòõ ü§ë ü§ó ü§ì üòé ü§° ü§† üòè üòí üòû üòî üòü üòï üôÅ" +
            " ‚òπÔ∏è üò£ üòñ üò´ üò© üò§ üò† üò° üò∂ üòê üòë üòØ üò¶ üòß üòÆ üò≤ üòµ üò≥ üò± üò® üò∞ üò¢ üò• ü§§ üò≠ üòì üò™ üò¥ üôÑ ü§î ü§• üò¨ ü§ê ü§¢ ü§ß üò∑ ü§í ü§ï üòà üëø" +
            " üëπ üë∫ üí© üëª üíÄ ‚ò†Ô∏è üëΩ üëæ ü§ñ üéÉ üò∫ üò∏ üòπ üòª üòº üòΩ üôÄ üòø üòæ üëê üôå üëè üôè ü§ù üëç üëé üëä ‚úä ü§õ ü§ú ü§û ‚úåÔ∏è ü§ò üëå üëà üëâ üëÜ üëá ‚òùÔ∏è ‚úã ü§ö" +
            " üñê üññ üëã ü§ô üí™ üñï ‚úçÔ∏è ü§≥ üíÖ üññ üíÑ üíã üëÑ üëÖ üëÇ üëÉ üë£ üëÅ üëÄ üó£ üë§ üë• üë∂ üë¶ üëß üë® üë© üë±‚Äç‚ôÄÔ∏è üë± üë¥ üëµ üë≤ üë≥‚Äç‚ôÄÔ∏è üë≥ üëÆ‚Äç‚ôÄÔ∏è üëÆ üë∑‚Äç‚ôÄÔ∏è üë∑" +
            " üíÇ‚Äç‚ôÄÔ∏è üíÇ üïµÔ∏è‚Äç‚ôÄÔ∏è üïµÔ∏è üë©‚Äç‚öïÔ∏è üë®‚Äç‚öïÔ∏è üë©‚Äçüåæ üë®‚Äçüåæ üë©‚Äçüç≥ üë®‚Äçüç≥ üë©‚Äçüéì üë®‚Äçüéì üë©‚Äçüé§ üë®‚Äçüé§ üë©‚Äçüè´ üë®‚Äçüè´ üë©‚Äçüè≠ üë®‚Äçüè≠ üë©‚Äçüíª üë®‚Äçüíª üë©‚Äçüíº üë®‚Äçüíº üë©‚Äçüîß üë®‚Äçüîß üë©‚Äçüî¨ üë®‚Äçüî¨" +
            " üë©‚Äçüé® üë®‚Äçüé® üë©‚Äçüöí üë®‚Äçüöí üë©‚Äç‚úàÔ∏è üë®‚Äç‚úàÔ∏è üë©‚ÄçüöÄ üë®‚ÄçüöÄ üë©‚Äç‚öñÔ∏è üë®‚Äç‚öñÔ∏è ü§∂ üéÖ üë∏ ü§¥ üë∞ ü§µ üëº ü§∞ üôá‚Äç‚ôÄÔ∏è üôá üíÅ üíÅ‚Äç‚ôÇÔ∏è üôÖ üôÖ‚Äç‚ôÇÔ∏è üôÜ üôÜ‚Äç‚ôÇÔ∏è üôã üôã‚Äç‚ôÇÔ∏è ü§¶‚Äç‚ôÄÔ∏è ü§¶‚Äç‚ôÇÔ∏è ü§∑‚Äç‚ôÄ" +
            "Ô∏è ü§∑‚Äç‚ôÇÔ∏è üôé üôé‚Äç‚ôÇÔ∏è üôç üôç‚Äç‚ôÇÔ∏è üíá üíá‚Äç‚ôÇÔ∏è üíÜ üíÜ‚Äç‚ôÇÔ∏è üï¥ üíÉ üï∫ üëØ üëØ‚Äç‚ôÇÔ∏è üö∂‚Äç‚ôÄÔ∏è üö∂ üèÉ‚Äç‚ôÄÔ∏è üèÉ üë´ üë≠ üë¨ üíë üë©‚Äç‚ù§Ô∏è‚Äçüë© üë®‚Äç‚ù§Ô∏è‚Äçüë® üíè üë©‚Äç‚ù§Ô∏è‚Äçüíã‚Äçüë© üë®‚Äç‚ù§Ô∏è‚Äçüíã‚Äçüë® üë™ üë®‚Äçüë©‚Äçüëß" +
            " üë®‚Äçüë©‚Äçüëß‚Äçüë¶ üë®‚Äçüë©‚Äçüë¶‚Äçüë¶ üë®‚Äçüë©‚Äçüëß‚Äçüëß üë©‚Äçüë©‚Äçüë¶ üë©‚Äçüë©‚Äçüëß üë©‚Äçüë©‚Äçüëß‚Äçüë¶ üë©‚Äçüë©‚Äçüë¶‚Äçüë¶ üë©‚Äçüë©‚Äçüëß‚Äçüëß üë®‚Äçüë®‚Äçüë¶ üë®‚Äçüë®‚Äçüëß üë®‚Äçüë®‚Äçüëß‚Äçüë¶ üë®‚Äçüë®‚Äçüë¶‚Äçüë¶ üë®‚Äçüë®‚Äçüëß‚Äçüëß üë©‚Äçüë¶ üë©‚Äçüëß" +
            " üë©‚Äçüëß‚Äçüë¶ üë©‚Äçüë¶‚Äçüë¶ üë©‚Äçüëß‚Äçüëß üë®‚Äçüë¶ üë®‚Äçüëß üë®‚Äçüëß‚Äçüë¶ üë®‚Äçüë¶‚Äçüë¶ üë®‚Äçüëß‚Äçüëß üëö üëï üëñ üëî üëó üëô üëò üë† üë° üë¢ üëû üëü üëí üé© üéì üëë ‚õë üéí üëù üëõ üëú üíº üëì" +
            " üï∂ üåÇ ‚òÇÔ∏è";

        const EmojiArr = EMOJIS.split(' ');
        const groupNum = Math.ceil(EmojiArr.length / (24));
        const items = [];

